.app-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 10px;
  width: 80%;
  margin-left: 15%;
}


body {
  background-image: linear-gradient(to top, #fbc2eb 0%, #a6c1ee 100%);
  background-attachment: fixed;
}



.contenedorDerecho {
  padding: 0;
  padding: 0px;

}

.contenedorIzquierdo {
  padding: 0;
  padding: 0px;
  margin-right: 5%;
}