
  
#completadas {
    font-family: Arial, Helvetica, sans-serif;
    color: #ffffff;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.4);
    font-size: 2.3vw;

    width: 100%;
    margin-top: 0;
    margin-bottom: 5%;
    margin-left: 10%
}

@import url('https://fonts.googleapis.com/css?family=Saira+Semi+Condensed');





.first-container {
    cursor: pointer;
    margin-top: 6%;
    margin-bottom: 0px;

    position: relative;
}

.text_Task {
    position: relative;
    letter-spacing: 2px;
    font-size: 5vw;
    margin-bottom: 0;
    margin-top: 3%;
}

span {
    font-family: Arial, Helvetica, sans-serif;
    color: #ffffff;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.4);
    display: inline-block;
    position: relative;
    margin-right: 5px;


}

span::after {
    content: '';
    position: absolute;
    height: 100%;
    width: 0;
    top: 0;
    left: 0;
    transition: 0.6s;
    z-index: -5;
}

@keyframes rotate-effect {
    from {
        transform: rotate(-30deg);
    }

    to {
        transform: rotate(20deg);
    }
}

.share:hover>h1>span:nth-child(1)::after {
    background: white;
    width: 100%;
    transition: 0.6s;
    animation: rotate-effect 0.2s linear;
}

.share:hover>h1>span:nth-child(1) {
    color: #39393B;
    transition: 0.22s;
}

