#inputBuscar {
    text-align: center;
    background-color: rgba(255, 255, 255, 0.2);
    border: none;
    border-radius: 50px;
    color: #7a7878;
    font-size: 16px;
    padding: 10px;
    width: 65%;
    outline: none;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    margin-left: 2%;
}

#inputBuscar::placeholder {
    color: rgba(119, 116, 116, 0.8);
}

#inputBuscar:focus {
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

.iconoLupa{
    height: 25px;
    margin-left: 3%;
    }