.todo-item {
  display: flex;
  margin-left: -35px;
}

.contenedorLi {
  width: 75%;
  display: flex;
  margin-left: 3%;
  align-items: center;

}


.textoElemento_Tasks {
  margin-right: auto;
  margin-left: 5%;
  font-family: 'Bangers', cursive;
  font-size: 16px;
  color: #585858;
  line-height: 1.4;
  font-size: 1.5vw;
}



.botonCheck {
  margin-right: 5%;
  color: blue;
  font-size: 2vw;
  opacity: 0.4; 
  transition: opacity 0.3s ease; 
  cursor: pointer;
}


.botonDelete {
  margin-left: 5%;
  color: red;
  font-size: 2vw;
  opacity: 0.2; 
  transition: opacity 0.3s ease; 
  cursor: pointer;
}

.botonDelete:hover {
  opacity: 1; 
  animation: shake 0.4s;
  font-size: 3vw;
}

.botonCheck:hover {
  opacity: 1; 
 
}


.Icon-check--active{
  color: gray;
}


.TodoItem-p--complete{
  text-decoration: line-through;
  opacity: 0.3;
}





@keyframes shake {
  0% {
    transform: translateX(0);
  }
  20% {
    transform: translateX(-5px);
  }
  40% {
    transform: translateX(5px);
  }
  60% {
    transform: translateX(-5px);
  }
  80% {
    transform: translateX(5px);
  }
  100% {
    transform: translateX(0);
  }
}