
  .contenedorHidden {
    display: flex;
    align-items: center;
    margin-left: 20%; 
    opacity: 0.2;
    transition: opacity 0.3s ease;
    font-size: 1.8vw;
    width: 35%;
    cursor: pointer;
  }
  
  .contenedorHidden:hover {
    opacity: 1;
  }
  
  

  h5{
